@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@500&display=swap);
body, html {
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

header.dash {
    padding: 0 10%;
    font-family: 'Nunito', sans-serif;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

span.text-o {
    color: orange;
}

span.bold {
    font-weight: 600;
}

header .circle {
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: orange;
    color: white;
}

main.dash {
    display: grid;
    grid-template-columns: 100%;
    grid-template-columns: 100%;
    padding-bottom: 7rem;
}

p {
    width: 80%;
    margin: auto;
}
.menu {
    background: white;
    border-radius: .4rem;
    padding-top: 1rem;

    max-width: 100vw;

    position: fixed;
    bottom: 0;

    text-align: right;
    box-shadow: 3px 2px 8px black;
    transition: .2s ease-in-out;
}

.menu strong{
    margin-right: 1rem;
    color: #5f5f5f;
}

.items {
    display: flex;
    overflow-x: scroll;
    padding: 0 1rem;
    margin-top: 1rem;
}

.item {
    padding: 30px;
    background: orange;
    margin: 5px;
    border: 0px solid black;
    border-radius: 10px;
    transition: .3s ease-in-out;
}

.item:hover {
    background: rgb(192, 125, 0);
}

.item.selected {
    background: rgb(192, 125, 0);
}

.menu i {
    color: white;
}
#home {
    background: 50% 10%/300px url(/static/media/winners.20319481.png) no-repeat;

    min-height: 100vh;
    font-family: 'Nunito', sans-serif;
    line-height: 1;

    padding-bottom: 3rem;

    grid-row: 1/2;
    grid-column: 1/2;
}

#home p {
    text-align: justify;
    color: #4d4d4d;
}

#home p ~ p {
    margin-top: 70%;
}
#events {
    grid-row: 1/2;
    grid-column: 1/2;
    padding-bottom: 3rem;

    opacity: 0;
    pointer-events: none;
}

.events-cards {
    display: flex;
    overflow-x: scroll;
    padding: 0 10px;
    margin-top: 4rem;
}

.card {
    border: 1px solid #CCC;
    border-radius: 10px;
    margin: 0 10px;
    background: orange;
    color: white;
}

.card-info {
    margin: 0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.card img {
    width: 18rem;
    border-radius: 10px 10px 0 0;
}

.card-body {
    margin: 0 10%;
}

.card-body p {
    margin: 1.4rem 0;
}
body, html {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    width: 100vw;
}

header {
    padding: .4rem 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header  img {
    width: 5rem;
}

.parent {
    min-height: 100vh;
    background: linear-gradient(200deg, #FFFFFF 0%, #E8E8E8 100%);
}

h1 {
    color: #F6B338;
    text-shadow: 5px 3px 10px #ccc;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 2rem;
}

main.main {
    background: 50% 10%/300px url(/static/media/people.ef93704d.png) no-repeat;
    height: 100%;
    position: absolute;
}

main.main p {
    text-align: justify;
    width: 70%;
    margin: auto;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 1.2rem;
    line-height: 1;
    color: #8D8D8D;
}
form {
    width: 90%;

    background: #FF9900;
    position: absolute;
    top: 24rem;
    left: 5%;

    display: flex;
    flex-direction: column;
    padding: 1rem 2rem 3rem;
    box-sizing: border-box;
    border-radius: 10px;

    font-family: 'Barlow Condensed', sans-serif;
}

form label {
    font-size: 1.2rem;
    font-weight: 500;

    position: relative;
}

form label i {
    position: absolute;
    bottom: 27%;
    left: 5px;
    color: rgb(0,0,0,.4);
}

form input {
    height: 2.3rem;
    width: 95%;
    margin: 1rem 0;
    padding: 0 10%;
    border-radius: 5px;
    border: none;
    box-sizing: border-box;
}

.subform {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    font-size: .7rem;
    height: 1rem;
}

.subform div {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
}

.subform label {
    margin-left: 5px;
    color: #F0F0F0;
}

.subform input {
    width: 1rem;
    border: 1px solid black;
}

.subform label {
    display: flex;
    flex-direction: row-reverse;
    font-size: .7rem;
}

.subform a {
    text-decoration: none;
    color: #8D8D8D;
}

form button {
    width: 95%;
    height: 3rem;
    margin-top: 2rem;
    border: 1px solid #ccc;
    background: #F0F0F0;
    font-size: 1.4rem;
    font-weight: 600;
    border-radius: 10px;
}

form .no-account {
    margin: 20px auto 0;
    font-size: .7rem;
}
